<template>
  <div>
    <MyHeader></MyHeader>
    <el-tag class="mytitle" type="success">HTML转MarkDown</el-tag>
    <div class="myapp">
      <div class="myinputarea">
        <div class="myinput">
          <el-tag>请输入HTML标签</el-tag>
          <el-input
              type="textarea"
              :rows="24"
              placeholder="请输入HTML标签"
              v-model="htmldata">
          </el-input>
        </div>
        <div class="mybutton">
          <el-button type="primary" round @click="HtmltoMD">转换</el-button>
        </div>
      </div>
      <div class="myoutputarea">
        <div class="myoutput">
          <el-tag>MardDown格式</el-tag>
          <el-input
              type="textarea"
              :rows="24"
              placeholder="MardDown"
              v-model="makedowndata">
          </el-input>
        </div>
      </div>
      <div class="myMD" >
        <el-tag type="success">预览</el-tag>
        <MavonEditor  :isPreview="true" v-model="makedowndata"/>
      </div>
    </div>
  </div>
</template>

<script>
import MyHeader from "@/components/Common/mobile/MyHeader";
import TD from "turndown"
import MavonEditor from 'vue-meditor'

var Td=new TD()
export default {
  name: "HtmlToMD",
  components: {MyHeader,MavonEditor},
  data(){
    return{
      htmldata:"",
      makedowndata:"",
    }
  },
  methods:{
    HtmltoMD(){
      this.makedowndata=Td.turndown(this.htmldata);
    }
  }
}
</script>

<style scoped>
.mytitle{
  margin: auto;
  display: flex;
  text-align: center;
  width: 80vw;
  flex-direction: column;
}
.myapp{
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  text-align: center;
  flex-direction: column;
  height: 100vh;
  margin: 20px auto;
}
.myMD{
}
</style>
